import http from '../../http-common'

class ProductsGroupsSercice {
  getAll () {
    return http.get('Products_Groups')
  }

  getAllPagnation (params) {
    return http.get('Products_GroupsPaging', { params })
  }

  get (id) {
    return http.get(`Products_Groups/getById?Id=${id}`)
  }

  search (params) {
    return http.get('Products_Groups/Search', { params })
  }

  create (data) {
    return http.post('Products_Groups', data)
  }

  update (id, data) {
    return http.put(`Products_Groups?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Products_Groups?Id=${id}`)
  }
}

export default new ProductsGroupsSercice()
