<template>
  <va-card :title="$t('tables.labelProductsGroups')" class="w-100">
    <va-button
      icon="fa fa-plus"
      flat
      slot="actions"
      class="mr-0"
      @click="showCreateModal"
    />
    <div class="row mr-0">
      <div class="flex xs12 sm4">
        <label class="input-label"> {{$t('forms.search')}} </label>
        <va-input v-model="searchText" @input="searchDelay" removable :placeholder="$t('forms.search')" @keyup.enter="search"  />
      </div>
      <!-- <va-button
        icon="fa fa-search"
        flat
        slot="actions"
        color="success"
        class="mr-0"
        @click="search"
      />
      <va-button
        icon="fa fa-trash"
        flat
        slot="actions"
        color="danger"
        class="mr-0"
        @click="deletesearch"
      /> -->

    </div>
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      :no-pagination = "searchBool"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        {{(props.rowIndex + 1) + ((pageNumber  -1) *10) }}
      </template>
      <template slot="name_Parent_Group" slot-scope="props">
        {{props.rowData.parent_Group === null ? '-----': props.rowData.parent_Group.name }}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="gray" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.edit') }}
        </va-button>

        <va-button flat small color="danger" @click="showDeleteModal(props.rowData)" class="ma-0">
          {{ $t('tables.delete') }}
        </va-button>
      </template>
    </va-data-table>

    <va-modal v-model="deleteModal" no-outside-dismiss
      :title=" $t('generic.deleteItemTitle') "
      :okText=" $t('modal.delete') "
      :cancelText=" $t('modal.cancel') "
      :message=" $t('generic.deleteItemMsg') "
      @ok="okDeletefun()"
      @cancel="cancleDeletefun()">
    </va-modal>
    <va-modal v-model="editModal" no-outside-dismiss
      size="large"
      :title=" $t('generic.editItemTitle') "
      hide-default-actions >
      <div class="row">
        <form @submit.prevent="okEditfun()">
          <div class="flex md12 sm12 xs12">
            <label class="input-label"> {{$t('placeholder.name') +' *'}} </label>
            <va-input v-model="currentobject.name"
              :error="riquierdvalidation(currentobject.name)"
              :error-messages="$t('errorMessage.required')"
              :placeholder="$t('placeholder.name')" required/>
          </div>
          <div class="flex md12 sm12 xs12">
            <label class="input-label"> {{$t('placeholder.parentGroup')}} </label>
            <va-select
              :label="$t('placeholder.parentGroup')"
              v-model="parants.value"
              searchable
              textBy="name"
              keyBy="id"
              :options="parants.options"
            />
          </div>

          <div class="d-flex justify--center mt-3">
            <va-button type="button" color="gray" flat @click="cancleEditfun()">
              {{ $t('modal.cancel') }}        </va-button>
            <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t('modal.edit') }}</va-button>
          </div>
        </form>

      </div>

    </va-modal>

    <va-modal v-model="createModal" no-outside-dismiss
      size="large"
      :title=" $t('generic.creatItemTitle') "
      hide-default-actions  :mobile-fullscreen="false"  >
      <form @submit.prevent="okCreatefun()">
        <div class="row">

          <div class="flex md12 sm12 xs12">
            <label class="input-label"> {{$t('placeholder.name')+' *'}} </label>
            <va-input v-model="currentobject.name"
              :error="riquierdvalidation(currentobject.name)"
              :error-messages="$t('errorMessage.required')"
              :placeholder="$t('placeholder.name')" required/>
          </div>
          <div class="flex md12 sm12 xs12">
            <label class="input-label"> {{$t('placeholder.parentGroup')}} </label>
            <va-select
              :label="$t('placeholder.parentGroup')"
              v-model="parants.value"
              searchable
              textBy="name"
              keyBy="id"
              :options="parants.options"
            />
          </div>
        </div>

        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="cancleCreatefun()">
            {{ $t('modal.cancel') }}        </va-button>
          <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t('modal.create') }}</va-button>
        </div>
      </form>

    </va-modal>
  </va-card>

</template>

<script>
import { debounce } from 'lodash'

import ProductsGroupsService from '../../../../services/BasicData/ProductsGroupsService'
import helper from '../../../../helper/helper'
export default {
  data () {
    return {
      perPage: 10,
      totalPages: 0,
      stopBtn: false,
      dataTable: [],
      parants: {
        value: '',
        options: [],
      },
      deleteModal: false,
      editModal: false,
      createModal: false,
      loading: true,
      opjectBeforeEdit: {},
      currentobject: {},
      searchText: '',
      searchBool: false,
      pageNumber: 1,
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',

      }, {
        name: 'name',
        title: this.$t('tables.headings.name'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|name',
      },
      {
        name: '__slot:name_Parent_Group',
        title: this.$t('tables.headings.name_Parent_Group'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|Parent_Group',
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  created () {
    this.readItems()
    this.getparents()
  },
  methods: {
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    numircvalidation (data) {
      return helper.numircvalidation(data)
    },
    numircriquierdvalidation (d) {
      return helper.numircriquierdvalidation(d)
    },

    /*  api Padination */
    sortCallBack (obj) {
      this.readItems(1, (obj[0].toString().charAt(0).toUpperCase() + obj[0].toString().slice(1)))
    },
    readItems (page = 1, orderBy = null) {
      this.pageNumber = page
      this.loading = true
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
        OrderBy: orderBy,
      }
      ProductsGroupsService.getAllPagnation(params)
        .then(response => {
          console.log(response.data)

          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getparents () {
      ProductsGroupsService.getAll()
        .then(response => {
          this.parants.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },

    searchDelay: debounce(function (searchBarCodeTerm) {
      this.search()
    }, 800),

    search () {
      this.loading = true
      if (this.searchText === '') {
        return this.readItems()
      }

      const params = {
        Text: this.searchText,
      }
      this.dataTable = []
      ProductsGroupsService.search(params)
        .then(response => {
          this.dataTable = response.data
          this.totalPages = Math.ceil(this.dataTable.length / 10)
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },

    deletesearch () {
      this.searchText = ''
      this.searchBool = false
      this.readItems()
    },
    create () {
      if (this.parants.value !== '') {
        this.currentobject.fK_Parent_Group = this.parants.value.id
      }

      ProductsGroupsService.create(this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.loading = false
          this.currentobject = {}
          this.parants.value = ''
          this.createModal = false
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.stopBtn = false
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        this.stopBtn = false
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    edit (user) {
      if (this.parants.value !== '') {
        this.currentobject.fK_Parent_Group = this.parants.value.id
      }
      ProductsGroupsService.update(this.currentobject.id, this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.currentobject = {}
          this.loading = false
          this.editModal = false
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.stopBtn = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    remove (id) {
      ProductsGroupsService.delete(id).then(response => {
        if (response.headers['x-status'] === 'true') {
          const idx = this.dataTable.findIndex(u => u.id === id)
          this.dataTable.splice(idx, 1)
          this.loading = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.stopBtn = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },

    /* Delete model */
    showDeleteModal (cuobject) {
      this.deleteModal = true
      this.currentobject = cuobject
      this.loading = true
    },
    okDeletefun () {
      this.remove(this.currentobject.id)
    },
    cancleDeletefun () {
      this.loading = false
      this.stopBtn = false
    },
    /* end Delete model */

    /* edit model */
    showEditModal (cuobject) {
      this.editModal = true
      if (cuobject.parent_Group !== null) {
        this.parants.value = cuobject.parent_Group
      }
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.currentobject = cuobject
      this.loading = true
    },
    okEditfun () {
      this.stopBtn = true
      this.edit(this.currentobject)
    },
    cancleEditfun () {
      this.editModal = false
      this.loading = false
      this.stopBtn = false
      console.log('ddd')
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.currentobject = {}
    },
    /* end edit model */

    /* create model */
    showCreateModal () {
      this.createModal = true
      this.loading = true
    },
    okCreatefun () {
      this.create()
      this.stopBtn = true
    },
    cancleCreatefun () {
      this.createModal = false

      this.loading = false
      this.stopBtn = false
      this.currentobject = {}
    },
    /* end create model */

  },
}

</script>

<style lang="scss">
  i.fa.fa-exclamation {
    color: red !important;
  }

</style>
